@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;500;700;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


body,html{
    margin: 0;
    padding: 0;
    font-size: 15px;
    font-weight: 400;
    line-height: 26px;
    color: #333;
    font-family: 'Open Sans', sans-serif!important;
}

*{
    box-sizing: border-box;
}


:root{
    --mainColor : #d3d3d4;
    --bgColor : #fc301e;
    --secondColor : #272f43;
    --thirdColor :#262e41;
}


h1,h2,h3,h4,h5,h6{
    font-size: 2rem;
    color: #272f43;
    color: var(--secondColor);
    font-weight: 500;
    line-height: 26px;
    margin: 10px 0;
}

p{
    font-size: 15px;
    font-weight: 500;
    margin: 0;
}

a{
    text-decoration: none!important;
    
}

/**************** ********************/

.navbar{
    width: 100%;
    height: auto;
    background-color: #d3d3d4;
    background-color: var(--mainColor);
}

.navbar img{
    width: 11rem;
}

.toggle{
    font-size: 33px;
    cursor: pointer;
    color: #333!important;
}

.anch a{
    color: #333!important;
    
}

.anch a.active{
    color: #fc301e!important;
    color: var(--bgColor)!important;
}

.css-6hp17o-MuiList-root-MuiMenu-list {
   width: 130px;
}


/********************* hero ************************/

.hero{
    width: 100%;
    height: auto;
    background-color: #d3d3d4;
    background-color: var(--mainColor);
    padding: 20px 0;
}


.carousel-dark .carousel-indicators [data-bs-target] {
    background-color: #fc301e!important;
    background-color: var(--bgColor)!important;
    width: 16px;
    height: 16px;
    border-radius: 50%!important;
}

.cation{
    width: 100%;
    height: auto;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    justify-content: center;

    
}

.cation h6{
    font-size: 1rem;
    font-weight: 600;
    text-transform: uppercase;
    margin: 0;
}
.cation h1 {
    font-size: 5rem;
    font-weight: 700;
    text-transform: capitalize;
    margin: 0;
    line-height: 84px;
    margin-left: 0;
    padding-left: 0;
}

.cation p{
    margin: 10px 0 0 0;
}

.cation .btn {
    margin-top: 28px;
}

.btnReadm{
    padding: 8px 20px!important;
    background-color: #272f43!important;
    background-color: var(--secondColor)!important;
    border-radius: 0!important;
    color: #fff!important;
    text-transform: uppercase!important;
    font-size: 14px!important;
    position: relative;
    transition: 0.3s;
    
}

.btnReadm:hover::before {
    content: "";
    position: absolute;
    top: -13px;
    left: -2px;
    width: 0;
    height: 0;
    border-bottom: 13px solid #fc301e;
    border-bottom: 13px solid var(--bgColor);
    border-left: 112px solid transparent;
    transition: 0.3s ease-out;
}

.cols{
    display: block;
   }

.carousel-indicators{
    display: block!important;
    text-align: center;
   }




